import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { useDebounced } from '../../hooks/useDebounce';
import { ReactComponent as SearchIcon } from '../../assets/icon/search.svg';

/**
 *   .
 * @param {Function} onSearch - Callback-,     .
 * @param {string} placeholder - -  input.
 * @param {number} debounceDelay -   debounce.
 * @param {string} inputClassName -    input.
 * @param {string} containerClassName -    .
 * @param {object} iconStyle -   ,  .
 */
const Search = ({
  onSearch,
  placeholder = 'Search',
  debounceDelay = 500,
  inputClassName = '',
  containerClassName = '',
  iconStyle = '',
}) => {
  const [query, setQuery] = useState('');

  //     debounce
  const debouncedQuery = useDebounced(query, debounceDelay);

  //  onSearch   debouncedQuery
  React.useEffect(() => {
    onSearch(debouncedQuery);
  }, [debouncedQuery, onSearch]);

  const handleInputChange = (event) => {
    setQuery(event.target.value);
  };

  return (
    <div className={`${containerClassName}`}>
      <SearchIcon className={`${iconStyle}`} />
      <input
        type="text"
        className={`${inputClassName}`}
        value={query}
        onChange={handleInputChange}
        placeholder={placeholder}
      />
    </div>
  );
};

Search.propTypes = {
  onSearch: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  debounceDelay: PropTypes.number,
  inputClassName: PropTypes.string,
  containerClassName: PropTypes.string,
  iconStyle: PropTypes.object,
};

export default Search;
