import { useState, useEffect } from 'react';
import { fetchSettings, updateSettings } from '../services/reader';

const useSettingsModal = () => {
  const [textSize, setTextSize] = useState(16);
  const [textWidth, setTextWidth] = useState(600);
  const [rowHeight, setRowHeight] = useState(1.5);
  const [font, setFont] = useState('Arial');
  const [theme, setTheme] = useState('light');

  useEffect(() => {
    const getSettings = async () => {
      try {
        const settings = await fetchSettings();
        setTextSize(settings.font_size); //   
        setTextWidth(settings.text_position); //   
        setRowHeight(settings.line_height);
        setFont(['Arial', 'Times New Roman', 'Courier New'][settings.font - 1]); //   
        setTheme(settings.theme);
      } catch (error) {
        console.error('  :', error);
      }
    };

    getSettings();
  }, []);

  const saveSettings = async (updatedSettings) => {
    const settings = {
      theme: updatedSettings.theme || theme,
      font_size: Math.round(updatedSettings.textSize || textSize), //         
      line_height: Math.round(updatedSettings.rowHeight || rowHeight), //    
      text_position: Math.round(updatedSettings.textWidth || textWidth), //         
      font_weight: 1, // ,   
      font: ['Arial', 'Times New Roman', 'Courier New'].indexOf(updatedSettings.font || font) + 1, //    
    };

    try {
      await updateSettings(settings);
    } catch (error) {
      console.error('  :', error);
    }
  };

  return {
    textSize,
    setTextSize,
    textWidth,
    setTextWidth,
    rowHeight,
    setRowHeight,
    font,
    setFont,
    theme,
    setTheme,
    saveSettings,
  };
};

export default useSettingsModal;
