export const selectUser = (state) => state.auth.user;
export const selectUsername = (state) => state.auth.username;
export const selectAccessToken = (state) => state.auth.accessToken; //   accessToken
export const selectRefreshToken = (state) => state.auth.refreshToken; //    refreshToken
export const selectError = (state) => state.auth.error;
export const isAuthenticated = (state) => {
  return !!state.auth.accessToken; //   accessToken
};
