import { GET_PROFILE_SUCCESS, GET_PROFILE_FAIL } from './profileTypes';
import { getProfile as fetchProfileFromApi } from '../../services/profile'; //    API 
import { jwtDecode } from 'jwt-decode'; //   
import { getAuthToken } from '../../services/api'; //    
import { logoutUser } from '../auth/authActions'; //   

//    
export const getProfile = () => async (dispatch) => {
  const { accessToken } = getAuthToken(); //  access   localStorage

  //    accessToken
  if (!accessToken) {
    dispatch({
      type: GET_PROFILE_FAIL,
      payload: 'Access token .  .',
    });
    return;
  }

  let username;
  try {
    //     username
    const decodedToken = jwtDecode(accessToken);

    // ,     username
    if (!decodedToken.username) {
      throw new Error('Username   ');
    }
    username = decodedToken.username;
  } catch (decodeError) {
    //   
    dispatch({
      type: GET_PROFILE_FAIL,
      payload: '  .  .',
    });
    logoutUser(); //     
    return;
  }

  try {
    //     username   API 
    const profile = await fetchProfileFromApi(username);
    dispatch({
      type: GET_PROFILE_SUCCESS,
      payload: profile, //     Redux
    });
  } catch (error) {
    //     
    const errorMessage =
      error.response && error.response.data
        ? error.response.data.detail || '    .'
        : '    .';

    if (error.response && error.response.status === 401) {
      //   401 (),   
      logoutUser();
    }

    dispatch({
      type: GET_PROFILE_FAIL,
      payload: errorMessage,
    });
  }
};
