import React from 'react';
import axios from 'axios';
import { ReactComponent as Upload } from '../../assets/icon/upload.svg';

function UploadButton({ buttonName, iconName }) {
  const fileInputRef = React.useRef();

  const handleClick = () => {
    fileInputRef.current.click();
  };

  const handleFileChange = async (event) => {
    const file = event.target.files[0];
    const formData = new FormData();
    formData.append('file', file);

    try {
      const response = await axios.post(
        'http://localhost:8000/api/studio/books/upload/',
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        },
      );
      console.log(response.data);
    } catch (error) {
      if (error.response) {
        //     ,     2xx
        console.error('Response data:', error.response.data);
        console.error('Response status:', error.response.status);
        console.error('Response headers:', error.response.headers);
      } else if (error.request) {
        //   ,    
        console.error('Request data:', error.request);
      } else {
        //  -   ,  
        console.error('Error message:', error.message);
      }
      console.error('There was an error uploading the file!', error);
    }
  };

  return (
    <div>
      <input
        type="file"
        ref={fileInputRef}
        style={{ display: 'none' }}
        onChange={handleFileChange}
      />
      <button className={buttonName} onClick={handleClick}>
        <Upload className={iconName} />
      </button>
    </div>
  );
}

export default UploadButton;
