import { configureStore } from '@reduxjs/toolkit';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage'; //  localStorage   
import rootReducer from './index'; //  
import authMiddleware from '../middleware/authMiddleware'; //   middleware

//  Redux Persist
const persistConfig = {
  key: 'root', //     
  storage, // ,    localStorage
  whitelist: ['profile', 'auth', 'register', 'nav'], //    ,  
};

const persistedReducer = persistReducer(persistConfig, rootReducer);

//  store
const store = configureStore({
  reducer: persistedReducer, //  persistReducer
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: ['persist/PERSIST'], //     redux-persist
      },
    }).concat(authMiddleware), //   middleware
  devTools: process.env.NODE_ENV !== 'production', //  devTools   
});

const persistor = persistStore(store);

export { store, persistor };
